@import url(https://fonts.googleapis.com/css?family=Open+Sans);
* {
    box-sizing: border-box;
    border: 0 solid #e2e8f0
}

img {
    border-style: solid
}

.rev-container-quote {
    width: 80%;
}

@font-face {
    font-family: Futura-medium;
    src: url('https://cdn.shopify.com/s/files/1/0316/1119/1435/files/futura_medium_bt.ttf?23723') format("woff2");
}

img,
video {
    display: block;
    max-width: 100%;
    height: auto
}

.main-para {
    font-family: Futura-medium;
}

.rv-cover {
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, -0.7)), to(rgba(0, 0, 0, 0.3))), url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/hero-image-d.jpg?v=1584393787");
    background-image: linear-gradient(rgba(0, 0, 0, -0.7), rgba(0, 0, 0, 0.3)), url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/hero-image-d.jpg?v=1584393787");
    padding-top: 36.458%;
}

@media (max-width: 640px) {
    .rv-cover {
        background-image: url("https://cdn.shopify.com/s/files/1/0114/4120/9408/files/hero-image-m.jpg?v=1584463086");
        padding-top: 126.5%;
    }
}

.rev-container {
    margin: 0 auto;
}

.rev-container {
    position: relative;
}

.rev-container .left-arrow {
    position: absolute;
    top: 40%;
    left: 14%;
    color: white;
    text-align: center;
    cursor: pointer;
}

.rev-container .right-arrow {
    position: absolute;
    top: 40%;
    right: 14%;
    color: white;
    text-align: center;
    cursor: pointer;
}

.rev-container .slick-slide {
    text-align: center;
    color: #419be0;
    background: white;
    margin: 20px;
}

.rev-container .video {
    position: relative;
}

.rev-container .video .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(4, 4, 4, 0.7);
}

.rev-container .nav-size {
    height: 30px;
    width: 30px;
}

.rev-container .slick-current .overlay {
    background: transparent;
    z-index: -99999;
}

.rev-container .playpause {
    display: none;
}

.rev-container .slick-current .playpause {
    display: block;
}

.playpause {
    background-image: url(https://cdn.shopify.com/s/files/1/0114/4120/9408/files/button-play.svg?v=1584643972);
    background-repeat: no-repeat;
    width: 50%;
    height: 50%;
    position: absolute;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    margin: auto;
    background-size: contain;
    background-position: center;
}

@media (max-width: 640px) {
    .rev-container {
        width: 100%;
    }
    .rev-container .left-arrow p {
        display: none;
    }
    .rev-container .left-arrow {
        top: 46%;
        left: 2%;
    }
    .rev-container .right-arrow p {
        display: none;
    }
    .rev-container .right-arrow {
        top: 46%;
        right: 2%;
    }
    .rev-container .nav-size {
        height: 25 px;
        width: 25px;
    }
}

.rv-banner {
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, -0.7)), to(rgba(0, 0, 0, 0.3))), url(https://cdn.shopify.com/s/files/1/0114/4120/9408/files/press-d.jpg?v=1584393787);
    background-image: linear-gradient(rgba(0, 0, 0, -0.7), rgba(0, 0, 0, 0.3)), url(https://cdn.shopify.com/s/files/1/0114/4120/9408/files/press-d.jpg?v=1584393787);
    width: 100%;
    overflow-x: hidden;
    padding-top: 10%;
    padding-bottom: 10%;
}

.nav-size-quote {
    width: 25%;
}

.left-arrow-quote {
    position: absolute;
    top: 40%;
    left: -70px;
}

.quotes h2 {
    text-align: center;
    padding-bottom: 10px;
}

.right-arrow-quote {
    position: absolute;
    top: 40%;
    right: -70px;
}

div#socialprev {
    top: 92%;
    left: 38%;
}

div#socialnext {
    top: 92%;
    right: 42%;
}

#socialSlide .slick-arrow {
    background: transparent;
}

@media (max-width: 640px) {
    .left-arrow-quote {
        position: absolute;
        top: 98%;
        left: 38%;
    }
    .right-arrow-quote {
        position: absolute;
        top: 98%;
        right: 42%;
    }
    .rv-banner {
        background-image: linear-gradient(rgba(0, 0, 0, -0.7), rgba(0, 0, 0, 0.3)), url(https://cdn.shopify.com/s/files/1/0114/4120/9408/files/press-m.jpg?v=1584463085);
        padding-top: 15%;
        padding-bottom: 15%;
    }
}

blockquote {
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    border: none;
    margin: 1.5em 10px;
    padding: 0.5em 10px;
    quotes: "\201C""\201D""\2018""\2019";
}

blockquote p:before {
    content: open-quote;
    font-weight: bold;
    line-height: 0.1em;
    margin-right: 0.10em;
    vertical-align: -0.4em;
    font-size: 4em;
    color: #89a79f;
    font-family: Georgia, "Times New Roman", Times, serif;
}

blockquote p:after {
    content: close-quote;
    font-weight: bold;
    font-size: 4em;
    line-height: 0.1em;
    margin-left: 0.10em;
    vertical-align: -0.4em;
    color: #89a79f;
    font-family: Georgia, "Times New Roman", Times, serif;
}

blockquote p {
    display: inline;
    font-size: 21px;
}

.reviews_bottom-1 .yotpo-bottom {
    padding-bottom: 30px;
}